.music_list {
  padding: 20px;
}
.music_list .ant-table-body::-webkit-scrollbar {
  background-color: #fff;
  width: 5px;
}
.music_list .ant-table-body::-webkit-scrollbar-thumb {
  height: 2px;
  border-radius: 3px;
  background: #C62F2F;
}
.music_list .table-row-active {
  background-color: #F5F5F5;
}
.music_list .ant-table-row {
  height: 5px !important;
}
.music_list td .index_sound span {
  display: inline-block;
}
.music_list td .index_sound img {
  display: none;
}
.music_list .active .index_sound span {
  display: none;
}
.music_list .active .index_sound img {
  display: inline-block;
}
