.new_songs .songs_item {
  border: 1px solid #F5F5F7;
  font-size: 10px;
}
.new_songs .songs_item .songs_item_content {
  list-style: none;
  padding: 0;
  width: '70vw';
  line-height: 50px;
  vertical-align: middle;
}
.new_songs .songs_item .songs_item_content li {
  background-color: #FAFAFA;
  padding: 2px 10px;
  position: relative;
  vertical-align: middle;
}
.new_songs .songs_item .songs_item_content li .song_item_left,
.new_songs .songs_item .songs_item_content li .song_item_mid {
  display: inline-block;
}
.new_songs .songs_item .songs_item_content li .song_item_left span:first-child {
  margin-right: 20px;
}
.new_songs .songs_item .songs_item_content li .song_item_left .sound {
  display: none;
}
.new_songs .songs_item .songs_item_content li .song_item_mid {
  position: absolute;
  left: 300px;
  width: 300px;
}
.new_songs .songs_item .songs_item_content li .song_item_mid span:last-child {
  position: absolute;
  left: 150px;
}
.new_songs .songs_item .songs_item_content li .album {
  width: 50px;
  margin: 0 10px;
}
.new_songs .songs_item .songs_item_content li span:last-child {
  float: right;
}
.new_songs .songs_item .songs_item_content li:nth-child(odd) {
  background-color: #F5F5F7;
}
.new_songs .songs_item .songs_item_content .active {
  background-color: #E3E3E5;
}
.new_songs .songs_item .songs_item_content .active .song_item_left span:first-child {
  display: none;
}
.new_songs .songs_item .songs_item_content .active .song_item_left .sound {
  display: inline-block;
  margin-right: 20px;
}
